import React, { useCallback, useEffect, useState } from 'react';
import 'fastclick';
import axios from '$base/tools/axios';
import { parse } from '$utils/parse';
import { PLANETS } from '$utils/consts';
import '$base/common_style/base.scss';
import bgUrl2 from '$base/imgs/wow.png';
import bgUrl1 from '$base/imgs/bgUrl1.png';
import bgUrl3 from '$base/imgs/bgUrl3.png';
import jx from '$base/imgs/jx.png';
import wyy from '$base/imgs/wyy.png';

import './index.less';

//  sentry 
const emptyFunction = () => {};
window.cb = emptyFunction;
window.popupreload = window.popupreload ?? emptyFunction;
window.api = window.api ?? {};
window.api.passsharetext = window.api.passsharetext ?? emptyFunction;
window.api.shareBackFromApp = window.api.shareBackFromApp ?? emptyFunction;
window.api.shareCb = window.api.shareCb ?? emptyFunction;
window.bringBack = window.bringBack ?? emptyFunction;
window.closeSelf = window.closeSelf ?? emptyFunction;
window.corona = window.corona ?? {
    info: emptyFunction,
    warn: emptyFunction,
    error: emptyFunction,
};

let result = {};
const params = parse(window.location.search);
try {
    result = JSON.parse(params.result);
    console.log(result, 'result');
} catch (e) {
    result = {};
}

const config = {
    '-1': {
        waitingButtonDesc: '',
        back: bgUrl1,
        button: jx
    },
    0: {
        waitingButtonDesc: '',
        back: bgUrl2,
        button: wyy
    },
    1: {
        waitingButtonDesc: '',
        back: bgUrl3,
        button: jx
    },
};

function Result() {
    const [count, setCount] = useState(result.count || 0);

    useEffect(() => {
        const timer = setInterval(() => {
            setCount((v) => (v - 1 >= 0 ? v - 1 : 0));
        }, 1000);
        return () => {
            clearInterval(timer);
        };
    }, []);

    let res;
    // 
    if (result.num !== 0 && result.myResult.length > 0) {
        res = 1;
    } else if (result.num !== 0 && result.myResult.length === 0) {
        res = -1;
    } else {
        res = 0;
    }

    const handleResult = useCallback(() => {
        const location = window.location;
        const pathname = location.pathname;
        let url;
        if (pathname.includes('result.html')) {
            url = `${location.origin}${pathname.replace('result.html', 'home.html')}`.split('?')[0];
        } else {
            url = `${location.origin}${`${pathname}/racehome.html`}`.split('?')[0];;
        }
        window.location.href = `${url}?roomId=${params?.roomId}`;
    }, []);

    return (
        <div
            className="m-result"
            style={{
                backgroundImage: `url(${config[res].back})`
            }}>
            <div
                className="m-result-planet"
                style={{
                    backgroundImage: `url(${PLANETS[result.starCode].result})`
                }}>
                <div className="m-result-planet-tips">
                    {PLANETS[result.starCode].cname}
                    
                </div>
                <div className="m-result-planet-winningusercount">
                    
                    <span>{result.winningUserCount || 0}</span>
                    
                </div>
                {
                    res !== 0 && (
                        <div className={`m-result-planet-send m-result-planet-send-${res}`}>
                            {result.num || ''}
                        </div>
                    )
                }
            </div>
            {
                res === 1 && (
                    <React.Fragment>
                        <div className="m-result-subtitle">
                            
                        </div>
                        <div className="m-result-gifts">
                            {
                                result.myResult.map((v, index) => (`${v.name} x ${v.count}${index !== result.myResult.length - 1 ? ';' : ''}`))
                            }
                        </div>
                    </React.Fragment>
                )
            }
            <div className="m-result-footer">
                {
                    count === 0 ? (
                        <div
                            onClick={handleResult}
                            style={{
                                backgroundImage: `url(${config[res].button})`,
                            }}
                            className="m-result-footer-button" />
                    ) : (
                        <div className="m-result-footer-waitingbutton">
                            { config[res].waitingButtonDesc }
                            { count }
                            s
                        </div>
                    )
                }
            </div>
            {
                res === 1 && (
                    <div className="m-result-footer-text">
                        
                    </div>
                )
            }
        </div>
    );
}

export default Result;
